<template>
    <div class="PracticeArea">
        <div class="content">
            <div class="test-title">
                <div class="test-icon">
                    <span v-if="nowdate.nowlength===4">选择题</span>
                    <span v-else>判断题</span>
                </div>
                <span class="test-question">
                    {{nowdate.nowquestion}}
                </span>
            </div>
            <div v-if="nowdate.nowurl" class="test-img">
                <img :src="nowdate.nowurl">
            </div>
            <div class="answerb">
                <li v-for="(item, index) in nowdate.nowlength" :key="index">
                    <div v-if="item" class="option">
                        <span :class="[errornum===index+1?'error':'']" @click="checkanswer(index+1)" class="option-item">{{indexAz[index+1]}}</span>
                        <span class="chose-question">{{item}}</span>
                    </div>
                </li>
            </div>

            <div v-if="alertErrorActive" style="margin-top:3rem" class="alertError">
                <hr>
         
                <h1>正确答案:{{indexAz[nowdate.nowanswernum]}}</h1>
                <p style="line-height:2.5rem;word-break:break-all;font-size:1.5rem;letter-spacing:0.3rem;text-indent:2rem">{{nowdate.expalins}}</p>
                <button @click="btnnext"
                    style="position:fixed;top:50%;right:0%;padding:0.5rem 3rem;border:none;border-radius:2rem;font-weight:bold;background:green;color:white;">下一题</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'PracticeArea',
        props: ['AnswerGroups', 'nowdate'],
        data() {
            return {
                alertErrorActive: false,
                errornum:null,
         
                indexAz: {
                    1: 'A',
                    2: 'B',
                    3: 'C',
                    4: 'D'
                },

            }
        },
        created() {
            //初始化数据
            let nowanswer = this.AnswerGroups.Driveanswer.result[this.nowdate.PracticeNum] //获取当前题目顺序
            this.nowdate.nowId = nowanswer.id //赋值ID
            this.nowdate.nowquestion = nowanswer.question //赋值问题
            this.nowdate.nowurl = nowanswer.url //赋值图片
            this.nowdate.nowanswernum = nowanswer.answer //赋值当前题目正确答案          
            this.nowdate.expalins = nowanswer.expalins //赋值当前题目解析
            //循环赋值答案
            for (let key in nowanswer) {
                if (key.match('item')) {
                    this.nowdate.nowlength.push(nowanswer[key])
                }
            }
        },
        watch: {
            "nowdate.PracticeNum"() {
                this.nowdate.nowlength = []
                //初始化数据
                let nowanswer = this.AnswerGroups.Driveanswer.result[this.nowdate.PracticeNum] //获取当前题目顺序
                this.nowdate.nowId = nowanswer.id //赋值ID
                this.nowdate.nowquestion = nowanswer.question //赋值问题
                this.nowdate.nowurl = nowanswer.url //赋值图片
                this.nowdate.nowanswernum = nowanswer.answer //赋值当前题目正确答案
                this.nowdate.expalins = nowanswer.expalins //赋值当前题目解析
                //循环赋值答案
                for (let key in nowanswer) {
                    if (key.match('item')) {
                        this.nowdate.nowlength.push(nowanswer[key])
                    }
                }
            }
        },
        methods: {
            btnnext(){
                this.errornum=null
                this.alertErrorActive=false
          
                    //当题目完成时跳转页面
                if (Number(this.AnswerGroups.Answertrue) + Number(this.AnswerGroups.Answerfalse) >= this.AnswerGroups
                    .Driveanswer.result.length) {
                    alert('你完成了所有答题')
                    //重置本地储存信息为0
                    window.localStorage.PracticeNum = 0
                    window.localStorage.Practiceno = 0
                    window.localStorage.Practicetrue = 0
                    setTimeout(() => {
                        this.$router.push('/home')
                    }, 1000);
                    return
                }
                //每答一题加一次量保存到本地储存
                window.localStorage.PracticeNum = Number(window.localStorage.PracticeNum) + 1
                //赋值答题数量
                this.nowdate.PracticeNum = window.localStorage.PracticeNum
            },
            checkanswer(index) {
                if (this.alertErrorActive) {
                    return
                }
                if (index == this.nowdate.nowanswernum) {
                    console.log('答题正确')
                    window.localStorage.Practicetrue = Number(window.localStorage.Practicetrue) + 1
                    this.$emit('changetor', true)
                } else {
                    this.errornum=index
                  
                    this.alertErrorActive=true
                    console.log('答题错误')
                    window.localStorage.Practiceno = Number(window.localStorage.Practiceno) + 1
                    this.$emit('changetor', false)
                    //获取本地储存
                    let locationinfo = window.localStorage.ErrorBook
                    //将获取到的信息用数组保存
                    let newLocationinfo = []
                    for (let index = 0; index < locationinfo.split(',').length; index++) {
                        //删除空数组
                        if (locationinfo.split(',')[index] === '') {
                            continue;
                        }
                        //删除重复数组
                        if (newLocationinfo.indexOf(locationinfo.split(',')[index]) === -1) {
                            newLocationinfo.push(locationinfo.split(',')[index])
                        }
                    }
                    //获取错误题目的ID
                    let errorId = this.nowdate.nowId
                    //获取新生成的错误题目ID
                    newLocationinfo.push(errorId)
                    //错误题目总数提交到本地储存
                    window.localStorage.ErrorNum = newLocationinfo.length
                    //提交新生成的错误ID并且保存到本地存储
                    window.localStorage.ErrorBook = newLocationinfo.toString()
                    return
                }
                //当题目完成时跳转页面
                if (Number(this.AnswerGroups.Answertrue) + Number(this.AnswerGroups.Answerfalse) >= this.AnswerGroups
                    .Driveanswer.result.length) {
                    alert('你完成了所有答题')
                    //重置本地储存信息为0
                    window.localStorage.PracticeNum = 0
                    window.localStorage.Practiceno = 0
                    window.localStorage.Practicetrue = 0
                    setTimeout(() => {
                        this.$router.push('/home')
                    }, 1000);
                    return
                }
                //每答一题加一次量保存到本地储存
                window.localStorage.PracticeNum = Number(window.localStorage.PracticeNum) + 1
                //赋值答题数量
                this.nowdate.PracticeNum = window.localStorage.PracticeNum
            }
        },
    }
</script>


<style lang="less">
    .PracticeArea {
        margin-bottom: 5rem;
        display: flex;
        flex-wrap: wrap;

        .content {
            padding: 2rem;
            width: 100%;

            .test-title {

                font-size: 1.8rem;

                .test-icon {
                    display: inline-block;
                    font-size: 1.2rem;
                    background: yellowgreen;
                    border-top-left-radius: 20rem;
                    border-bottom-left-radius: 20rem;
                    border-top-right-radius: 20rem;
                    padding: 0.5rem 1rem;
                    margin-right: 1rem;

                    span {
                        color: white;
                    }
                }
            }

            .test-img {
                width: 100%;
                display: flex;
                justify-content: center;
                margin: 2rem 0;

                img {
                    max-width: 100%;
                }
            }

            .answerb {
                display: flex;
                flex-wrap: wrap;
                margin-top: 3rem;

                li {
                    width: 100%;
                    margin: 1rem 0;

                    .option {
                        font-size: 2rem;

                        .option-item {
                            &.error{
                                background: red;
                            }
                            &.success{
                                background: green;
                            }
                            padding: 0.5rem;
                            border-radius: 50%;
                            box-shadow: 0 0 0.5rem 0.2rem lightgray;
                            cursor: pointer;
                        }

                        .chose-question {
                            line-height: 2;
                            margin-left: 2rem;
                        }
                    }
                }
            }

        }
    }
</style>